<template>
  <div class="about">
    <h1>메모입니다</h1>
    <table>
      <tr>
        <td>번호</td>
        <td>제목</td>
        <td>내용</td>
        <td>작성자</td>
      </tr>
      <tr v-for="item in memo" :key="item.id">
        <td>{{item.id}}</td>
        <td>{{item.title}}</td>
        <td>{{item.memo}}</td>
        <td>{{item.writer}}</td>      
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  created() {
    //메모값을 create에 연결 : vue가 생성될때 가져오기
    //axios사용방법은 fetch와 거의 동일
    this.$http.get('/api/memo')
      .then((response)=>{
        console.log(response.data)
        this.memo = response.data
      })
  },
  data () {
    return {
      memo : []
    }
  }
}
</script>
